.SeasonsList{
    display: flex;
    flex-direction: row;
    margin-right: -16px;
    margin-left:-16px;
    margin-top: 8px;
    overflow-x:scroll;
    align-items: center;
    scrollbar-width: none;
    -ms-overflow-style: none;
    &::-webkit-scrollbar {
        width: 0;
        height: 0;
    }
    .Season{
        padding: 7px 18px;
        color: var(--color-2);
        text-align: center;
        white-space: nowrap;
        font-size: 20px;
        text-transform: uppercase;
    }
    .active{
        color: var(--color-5);
        position: relative;
        &::after{
            content: '';
            position: absolute;
            bottom: 0;
            left: 16px;
            right: 16px;
            border: 1px solid var(--color-5);
        }
    }
}