.Location{
    display: flex;
    flex-direction: column;
    width: calc(100vw - 32px);
    height: calc(0.64*(100vw - 32px));
    border-radius: 20px;
    overflow: hidden;
    &+.Location{
        margin-top: 24px;
    }
    img{
        display: block;
        position: relative;
        width: 100%;
        height: calc(0.44*(100vw - 32px));
    }
    .LocationTitleWrapper{
        height: 100%;
        background-color: var(--color-1);
        padding: 0 16px;
        display: flex;
        flex-direction: row;
        align-items: center;
    }
}