.Modal{
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: rgba(0, 0, 0, 0.5);
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;
    pointer-events: none;
    opacity: 0;
    z-index: 1000000000;
    &.Active{
        transition: opacity .2s 0s;
        pointer-events: all;
        opacity: 1;
        .ModalAuthorizationError{
            opacity: 1;
            transform: scale(1);
            transition: transform .2s .2s, opacity .2s .2s;
        }
    }

}
.ModalAuthorizationError{
    transform: scale(.2);
    opacity: 0;
    transition: transform .2s 0s, opacity .2s 0s;
    pointer-events:all;
    display: flex;
    flex-direction: column;
    padding: 30px;
    border-radius: 12px;
    background-color: var(--color-1);
    .Title{
        color: var(--color-5);
    }
    .Message{
        margin-top: 20px;
        color: var(--color-5);
    }
    .ButtonOk{
        margin-top: 24px;
        padding: 8px;
        text-align: center;
        color: var(--color-7);
        border: 1px solid var(--color-7);
        border-radius: 12px;
    }
}