.Main{
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    overflow: hidden;
    display: flex;
    flex-direction: column;
    align-items: center;
    &::after{
        content: '';
        position: absolute;
        left: 0;
        right: 0;
        top: 0;
        bottom: 0;
        background-size:contain;
        background-repeat: repeat;
        background-image: url('../../Icons/image\ 1.png');
        animation: backgroundFlash 4s linear infinite;
    }
    .Title{
        z-index: 100;
        display: block;
        margin-left: calc(.05*53vh);
        position: relative;
        height: 53vh;
        width: calc(.71*53vh);
        background-color: var(--color-3);
        mask-image: url('../../Icons/MainTitle.png');
        mask-position: center;
        animation: titleFlash 2s linear infinite;
        &::before{
            content: '';
            position: absolute;
            left: 0;
            right: 0;
            top: 0;
            bottom: 0;
            background-size: 100%;
            background-position: center;
            background-repeat: no-repeat;
            background-image: url('../../Icons/MainTitle.png');
           
        }
    }
    .Logo{
        z-index: 100;
        position: relative;
        height: calc(47vh - 60px);
        width: calc(1.28*(47vh - 60px));
        background-size: 90%;
        background-position: bottom;
        background-repeat: no-repeat;
        background-image: url('../../Icons/MainLogo.png');
    }
}

@keyframes titleFlash{
    0% {mask-size: 100%};
    50% {mask-size: 105%};
    100% {mask-size: 100%};
}

@keyframes backgroundFlash{
    0%{transform: scale(1)};
    50%{transform: scale(1.2)};
    100%{transform: scale(1)};
}