@mixin icon{
    min-width: 24px;
    height: 24px;
    mask-repeat: no-repeat;
    mask-position: center;
    mask-size: 100%;
    background-color: var(--color-2);
    transition: background-color .2s linear;
}


.Authorization{
    display: flex;
    flex-direction: column;
    align-items: center;
    position: relative;
    .MainLogo{
        position: relative;
        margin-top: 64px;
        // 286px - высота элементов регистрации
        // 64px - верзний отступ логотипа
        // 8.12vh - отступ между логотипом и элементами регстрации
        // 0.116 и 0.712 коэффициенты пропорциональности 
        // относительно высоты логотипа
        margin-left: calc(.0116 * (100vh - 64px - 286px - 8.12vh));
        width: calc(.712 * (100vh  - 64px - 286px - 8.12vh));
        height: calc(100vh - 64px - 286px - 8.12vh);
        background-color: var(--color-3);
        mask-image: url('../../Icons/AuthorizationBG.png');
        mask-position: center;
        animation: titleFlash 2s linear infinite;
        &::before{
            content: '';
            position: absolute;
            left: 0;
            right: 0;
            top: 0;
            bottom: 0;
            background-size:cover;
            background-position: center;
            background-repeat: no-repeat;
            background-image: url('../../Icons/AuthorizationBG.png');
        }
    }
    .Form{
        margin-top: 8.12vh;
        width: 85vw;
        max-width: 319px;
    }
    .Registration{
        margin-top: 24px;
        margin-bottom: 28px;
        display: flex;
        flex-direction: row;
        justify-content: center;
        align-items: center;
        .Caption{
            color: var(--color-2);
        }
        .Link{
            margin-left: 10px;
            color: var(--color-3);
        }
    }
}





@keyframes titleFlash{
    0% {mask-size: 100%};
    50% {mask-size: 103%};
    100% {mask-size: 100%};
}